import React from "react";
import { Link } from "react-router-dom";
import { Menu, Layout } from "antd";
import { useSelector, useDispatch } from 'react-redux';
import * as actionTypes from '../../Store/Actions/actionTypes';
import onLogout from '../Logout/Logout'; 

const NavigationBar = () => {
  const dispatch = useDispatch();
  const afterLogout = () => dispatch({type: actionTypes.AUTH_LOGOUT});
  const isAuthenticated = useSelector(state => state.auth.isAuthenticated);
  let loginLink = null;
  let logoutLink = null;
 
  if(isAuthenticated) {
    logoutLink = <Menu.Item key="logout">
                  Logout <Link to="/logout" onClick={() => onLogout(afterLogout)} />
                </Menu.Item>;
  } else {
    loginLink = <Menu.Item key="login">
                  Login <Link to="/login" />
                </Menu.Item>;
  } 
  
  const { Header } = Layout;
  return (
    <Header>
      <Menu mode="horizontal">
        <Menu.Item key="home">
          Home
          <Link to="/" />
        </Menu.Item>
        <Menu.Item key="streaming">
          Streaming
          <Link to="/streaming" />
        </Menu.Item>
        <Menu.Item key="help">
          Help
          <Link to="/help" />
        </Menu.Item>
        <Menu.Item key="about">
          About
          <Link to="/about" />
        </Menu.Item>
        <Menu.Item key="signup">
          Signup
          <Link to="/signup" />
        </Menu.Item>
        {loginLink}
        {logoutLink}
      </Menu>
    </Header>
  );
};

export default NavigationBar;
