import React from "react";
import BaseReactionButton from "./BaseReactionButton";

const SadReactionButton = ({ mediaId, mediaType }) => {
  return (
    <BaseReactionButton
      glyph="😔"
      reactionType="SADEMOJI"
      mediaType={mediaType}
      mediaId={mediaId}
    />
  );
};

export default SadReactionButton;
