import "./Login.scss";
import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { Input, Form } from "formik-antd";
import { Formik } from "formik";
import { Row, Col, Switch, Button } from "antd";
import { EllipsisOutlined } from "@ant-design/icons";
import axios from 'axios';
import * as Yup from "yup";
import * as actionTypes from '../../Store/Actions/actionTypes';
import URI from '../../Utils/utils.urls';

import LogoImage from "../../Resources/Images/logo.png";
import BackgroundImage from "../../Resources/Images/background-img.jpg";
import GoogleImage from "../../Resources/Images/google-logo.png";
import FacebookImage from "../../Resources/Images/fb-logo.png";

const LoginSchema = Yup.object().shape({
  username: Yup.string().required("Username is required"),
  password: Yup.string().required("Password is required")
});

class Login extends Component {

  submitHandler = ( authData ) => {
    this.props.onAuthStart();
    axios.post(URI.authentication.loginURL(), authData)
      .then(response => {
          this.props.onAuthSuccess(authData.username, response);
          let expiredIn = parseInt(localStorage.getItem('expiredAt')) * 1000 - new Date().getTime();;
          setTimeout(() => {
            this.props.onAuthLogout();
         }, expiredIn);
      })
      .catch(err => {
          this.props.onAuthFail(err);
      });
  }

  render() {
    
    let errorMessage = null;
    if (this.props.error) {
      errorMessage = (
          <div class="error">Username or Password is incorrect</div>
      );
    }

    let authRedirect = null;
    if (this.props.isAuthenticated) {
      authRedirect = <Redirect to='/'/>
    }

    return (<div>
       {authRedirect}
       <Formik  
        initialValues={{
          username: "",
          password: "",
        }}
        validationSchema={LoginSchema}
        onSubmit={(authData) => {
          this.submitHandler(authData);
        }}
      >
        {({ errors, touched }) => (
          <div class="login-page">
            <Row>
              <Col xs={24} sm={24} md={14} lg={12}>
                <div class="login-container">
                  <div
                    class="form-wrapper"
                    style={{ backgroundImage: `url(${BackgroundImage})` }}
                  >
                    <div class="form-container">
                      <div class="logo">
                        <a href="#">
                          <img src={LogoImage} alt="Versenation Logo" />
                        </a>
                      </div>
                      <Form class="login-form">
                        {errorMessage}
                        <Input name="username" placeholder="Username" />
                        {errors.username && touched.username ? (
                          <div class="error">{errors.username}</div>
                        ) : null}

                        <Input.Password name="password" placeholder="Password" />
                        {errors.password && touched.password ? (
                          <div class="error">{errors.password}</div>
                        ) : null}

                        <div class="fp-link">
                          <a href="#">Forgot password?</a>
                        </div>
                        <div class="login-btn">
                          <button type="submit">Login</button>
                        </div>
                        <div class="remember-me">
                          <Switch size="small" defaultChecked /> Remember me
                        </div>
                      </Form>
                      <div class="social-login">
                        <p>Continue with</p>
                        <ul>
                          <li>
                            <a href="#" class="btn fb-btn">
                              <span>
                                <img src={FacebookImage} alt="Facebook Logo" />
                              </span>{" "}
                              Facebook
                            </a>
                          </li>
                          <li>
                            <a href="#" class="btn ggl-btn">
                              <span>
                                <img src={GoogleImage} alt="Google Logo" />
                              </span>
                              Google
                            </a>
                          </li>
                        </ul>
                        <p>
                          <span class="dots">
                            <EllipsisOutlined />
                          </span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </Col>
              <Col xs={24} sm={24} md={10} lg={12}>
                <div class="login-description">
                  <div class="login-description-wrapper">
                    <h1>
                      Welcome to
                      <br />
                      <span>
                        Verse Nation
                        <br />
                        Marketplace
                      </span>
                    </h1>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        )}
      </Formik>
    </div>
    )
  }
}

const mapStateToProps = state => {
  return {
      loading: state.auth.loading,
      error: state.auth.error,
      isAuthenticated: state.auth.token !== null
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onAuthStart: () =>  dispatch({type: actionTypes.AUTH_START}),
    onAuthSuccess: (username, token) =>  dispatch({type: actionTypes.AUTH_SUCCESS, username: username, token: token}),
    onAuthFail: (err) => dispatch({type: actionTypes.AUTH_FAIL, err: err}),
    onAuthLogout: () => dispatch({type: actionTypes.AUTH_LOGOUT})
  }
};
export default connect(mapStateToProps, mapDispatchToProps)(Login);
