import React from "react";
import BaseReactionButton from "./BaseReactionButton";

const RaisedFistReactionButton = ({ mediaId, mediaType }) => {
  return (
    <BaseReactionButton
      glyph="✊🏿"
      reactionType="BLACKPOWERFIST"
      mediaType={mediaType}
      mediaId={mediaId}
    />
  );
};

export default RaisedFistReactionButton;
