import React from "react";
import { Avatar } from "antd";
import { AiOutlineUser } from "react-icons/ai";
import FollowButton from "../FollowButton";

import "./FollowArtistTile.scss";

const FollowArtistTile = ({ imgUrl, username, profileUrl }) => {
  return (
    <div className="artist-tile">
      <a href={profileUrl}>
        <Avatar
          className="avatar"
          size={58}
          icon={!imgUrl ? <AiOutlineUser /> : null}
          src={imgUrl ? imgUrl : null}
        />
        <div className="body">
          <div className="username">{username}</div>
          <FollowButton />
        </div>
      </a>
    </div>
  );
};

export default FollowArtistTile;
