import React from "react";
import { NavLink } from "react-router-dom";

import "./BaseNavLink.scss";

// TODO: Implement styles for selected link items
const BaseNavLink = ({ Icon, to, children }) => {
  return (
    <NavLink
      className="base-navlink"
      activeClassName="base-navlink--selected"
      to={to}
    >
      <span className="icon">{Icon}</span>
      <span className="text">{children}</span>
    </NavLink>
  );
};

export default BaseNavLink;
