import React from "react";
import { Layout } from "antd";

import "./ColumnLayout.scss";

const { Header, Sider, Content } = Layout;

const ColumnLayout = ({
  HeaderContent,
  LeftSidebarContent,
  BodyContent,
  RightSidebarContent,
  leftSidebarWidth = 200,
  rightSidebarWidth = 200,
}) => {
  return (
    <Layout className="col-layout">
      <Header className="header">{HeaderContent}</Header>
      <Layout className="main-section">
        <Sider className="left-sidebar" width={leftSidebarWidth}>
          {LeftSidebarContent}
        </Sider>
        <Content className="body">{BodyContent}</Content>
        {RightSidebarContent ? (
          <Sider className="right-sidebar" width={rightSidebarWidth}>
            {RightSidebarContent}
          </Sider>
        ) : null}
      </Layout>
    </Layout>
  );
};

export default ColumnLayout;
