
import axios from 'axios';
import URI from '../../Utils/utils.urls';

const Logout = (afterLogout) => {
        const headers = { 'refresh-token': localStorage.getItem('refreshToken') };
        axios.post(URI.authentication.logoutURL(), null, {
                headers: headers
            })
            .then(response => {
                afterLogout();
            })
            .catch(err => {
                console.log(err);
            });

}
export default Logout;
