import React from "react";
import BaseReactionButton from "./BaseReactionButton";

const ThumbsDownReactionButton = ({ mediaId, mediaType }) => {
  return (
    <BaseReactionButton
      glyph="👎"
      reactionType="THUMBSDOWN"
      mediaType={mediaType}
      mediaId={mediaId}
    />
  );
};

export default ThumbsDownReactionButton;
