import jwt from 'jsonwebtoken';
import * as actionTypes from '../Actions/actionTypes';
import { updateObject } from '../../Utils/utility';

const initialState = {
    token: null,
    userId: null,
    username: null,
    isAuthenticated: false,
    accountType: null,
    error: null,
    loading: false
};

const authStart = ( state, action ) => {
    return updateObject( state, { error: null, loading: true } );
};

const authSuccess = (state, action) => {
    var decoded = jwt.decode(action.token.data.accessToken, {complete: true});
    localStorage.setItem('accessToken', action.token.data.accessToken);
    localStorage.setItem('refreshToken', action.token.data.refreshToken);
    localStorage.setItem('expiredAt', decoded.payload.exp);
    localStorage.setItem('issuedAt', decoded.payload.iat);
    localStorage.setItem('userId', decoded.payload.sub);
    localStorage.setItem('username', action.username);
    localStorage.setItem('accountType', action.token.data.accountType);
    return updateObject( state, { 
        token: action.token.data,
        username: action.username,
        userId: decoded.payload.sub,
        accountType: action.token.data.accountType,
        isAuthenticated: true,
        error: null,
        loading: false
     } );
};

const authFail = (state, action) => {
    return updateObject( state, {
        token: null,
        isAuthenticated: false,
        error: action.err.message,
        loading: false,
    });
};

const authLogout = (state, action) => {
    localStorage.removeItem('accessToken');
    localStorage.removeItem('refreshToken');
    localStorage.removeItem('expiredAt');
    localStorage.removeItem('issuedAt');
    localStorage.removeItem('userId');
    localStorage.removeItem('username');
    localStorage.removeItem('accountType');
    return updateObject(state, initialState);
};

const reducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case actionTypes.AUTH_START: return authStart(state, action);
        case actionTypes.AUTH_SUCCESS: return authSuccess(state, action);
        case actionTypes.AUTH_FAIL: return authFail(state, action);
        case actionTypes.AUTH_LOGOUT: return authLogout(state, action);
        default:
            return state;
    }
};

export default reducer;