import React from "react";
import { Button } from "antd";
import { ShareAltOutlined } from "@ant-design/icons";

import "./ShareButton.scss";

const ShareButton = ({ onClick }) => {
  return (
    <Button
      className="share-btn"
      type="primary"
      icon={<ShareAltOutlined />}
      onClick={onClick}
    >
      Share
    </Button>
  );
};

export default ShareButton;
