import React from "react";
import BaseReactionButton from "./BaseReactionButton";

const PoopReactionButton = ({ mediaId, mediaType }) => {
  return (
    <BaseReactionButton
      glyph="💩"
      reactionType="SHITEMOJI"
      mediaType={mediaType}
      mediaId={mediaId}
    />
  );
};

export default PoopReactionButton;
