const URI = {};
const BasePath = 'http://70284e6bb734.ngrok.io/';  //'http://localhost:8080/';

URI.authentication = URI.authentication || (function () {
  return {
    loginURL: function () {
      return BasePath + 'api/authenticate/login' ;
    },
    artistSignupURL: function () {
      return BasePath + 'api/authenticate/signup/creator' ;
    },
    fanSignupURL: function () {
      return BasePath + 'api/authenticate/signup/fan' ;
    },
    logoutURL: function () {
      return BasePath + 'api/authenticate/logout' ;
    }
  };
}());

export default URI;
