import React, { useState, useEffect } from "react";
import { List, Button } from "antd";
import { RiArrowUpSLine, RiArrowDownSLine } from "react-icons/ri";
import { default as APIClient } from "../../Services/VerseNationAPIClient";
import ArtistTile from "../FollowArtistTile";

import "./TrendingArtistList.scss";

const TrendingArtistList = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    const apiClient = new APIClient();

    const getTopUsers = async () => {
      const topUsers = await apiClient.getTopUsers(console.error);
      setData(topUsers);
    };

    getTopUsers();
  }, []);

  // TODO: Scroll on button click functionality needs to be added
  return (
    <div className="trending-artist-list">
      <label className="label">Trending Artists</label>
      <Button
        className="scoll-up-arrow"
        type="text"
        shape="circle"
        icon={<RiArrowUpSLine />}
      />
      <div className="list-container">
        <List
          dataSource={data}
          renderItem={(item) => (
            <List.Item key={item.id}>
              <ArtistTile imgUrl={item.profilePic} username={item.userName} />
            </List.Item>
          )}
          split={false}
        />
      </div>
      <Button
        className="scoll-down-arrow"
        type="text"
        shape="circle"
        icon={<RiArrowDownSLine />}
      />
    </div>
  );
};

export default TrendingArtistList;
