import React from "react";
import { Typography } from "antd";
import ProfileTile from "../ProfileTile";
import PostMedia from "../PostMedia";
import ReactionBar from "../ReactionsBar";
import ShareButton from "../ShareButton";
import RepostButton from "../RepostButton";
import { isUri } from "valid-url";

import "./TimelinePost.scss";

const { Paragraph } = Typography;

const PostHeader = ({ imgUrl, username, profileUrl, timestamp }) => {
  return (
    <div className="post-header">
      <ProfileTile
        imgUrl={imgUrl}
        username={username}
        profileUrl={profileUrl}
      />
      <Paragraph className="timestamp">{timestamp}</Paragraph>
    </div>
  );
};

const PostBody = (props) => {
  const { caption, postType, body, ...otherProps } = props;

  return (
    <div className="post-body">
      <div className="caption">
        <Paragraph ellipsis={{ rows: 5, expandable: true, symbol: "more" }}>
          {caption}
        </Paragraph>
      </div>
      <div className="body">
        {
          isUri(body.trim()) ? (
            <PostMedia postType={postType} url={body.trim()} {...otherProps} />
          ) : (
            body
          )
          // isUri(body.trim()) ? <PostMedia mediaType={postType} url={body.trim()} {...otherProps} /> : body
        }
      </div>
    </div>
  );
};

const PostFooter = () => {
  return (
    <div className="post-footer">
      <ReactionBar />
      <div className="action-btn-group">
        <span className="repost">
          <RepostButton />
        </span>
        <span className="share">
          <ShareButton />
        </span>
      </div>
    </div>
  );
};

const TimelinePost = (props) => {
  const {
    imgUrl,
    username,
    profileUrl,
    timestamp,
    caption,
    postType,
    body,
  } = props;

  return (
    <div className="timeline-post">
      <PostHeader
        imgUrl={imgUrl}
        username={username}
        profileUrl={profileUrl}
        timestamp={timestamp}
      />
      <PostBody caption={caption} postType={postType} body={body} />
      <PostFooter />
    </div>
  );
};

export default TimelinePost;
