import axios from "axios";
import { API_BASE_URL } from "../Util/Config.json";

export default class VerseNationAPIClient {
  // Common REST method wrappers
  // ===========================
  async get(path, onError) {
    const endpoint = `${API_BASE_URL}/${path}`;
    try {
      const res = await axios.get(endpoint);
      return res.data;
    } catch (err) {
      onError && onError(err);
    }
  }

  async post(path, payload, onError) {
    const endpoint = `${API_BASE_URL}/${path}`;
    try {
      const res = await axios.post(endpoint, payload);
      return res.data;
    } catch (err) {
      onError && onError(err);
    }
  }

  // Public (No auth required)
  // =========================
  async getTopUsers(onError) {
    return await this.get("public/topusers", onError);
  }

  // Timeline
  // ========
  async reactToPost(postId, reactionType, onError) {
    const path = `post/react/postId=${postId}&reactionType=${reactionType}`;
    return await this.post(path, {}, onError);
  }
}
