import React, { useState } from "react";
import { Input, Select, Checkbox, Form } from "formik-antd";
import { Formik } from "formik";
import { Row, Col, Alert } from 'antd';
import { EllipsisOutlined } from "@ant-design/icons";
import * as Yup from "yup";
import axios from "axios";
import URI from "../../Utils/utils.urls";

import "./Signup.scss";
import BackgroundImage from "../../Resources/Images/background-img.jpg";
import LogoImage from "../../Resources/Images/logo.png";
import GoogleImage from "../../Resources/Images/google-logo.png";
import FacebookImage from "../../Resources/Images/fb-logo.png";

const GenreRange = ['Blues', 'Hip-Hop/Rap', 'R&B/Soul', 'Classical', 'Jazz', 'Custom', 'Country', 'Pop'];

const SignupSchema = Yup.object().shape({
  firstName: Yup.string()
      .min(2, "Too Short!")
      .max(50, "Too Long!")
      .required("First name is required"),
  lastName: Yup.string()
      .min(2, "Too Short!")
      .max(50, "Too Long!")
      .required("Last name is required"),
  birthMonth: Yup.string().required("Required"),
  birthDate: Yup.string().required("Required"),
  birthYear: Yup.string().required("Required"),
  email: Yup.string()
      .email("Email address is invalid")
      .required("Email is required"),
  password: Yup.string()
      .min(6, "Too Short!")
      .max(20, "Too Long!")
      .required("Password is required"),
  passwordAgain: Yup.string()
      .oneOf([Yup.ref('password'), null], 'Passwords do not match')
      .required("Password confirmation is required"),
  city: Yup.string()
      .min(1, "Too Short!")
      .max(50, "Too Long!")
      .required("City is required"),
  state: Yup.string().required("State is required"),
  zip: Yup.string()
      .min(1, "Too Short!")
      .max(6, "Too Long!")
      .required("ZIP code is required"),
  accountType: Yup.string().required("Select an account type"),
  bio: Yup.string()
      .min(1, "Too Short!")
      .max(300, "Too Long!")
      .required("Bio is required"),
  genre: Yup.array().required('Select a genre range'),
});

const States = [
  "AK - Alaska",
  "AL - Alabama",
  "AR - Arkansas",
  "AS - American Samoa",
  "AZ - Arizona",
  "CA - California",
  "CO - Colorado",
  "CT - Connecticut",
  "DC - District of Columbia",
  "DE - Delaware",
  "FL - Florida",
  "GA - Georgia",
  "GU - Guam",
  "HI - Hawaii",
  "IA - Iowa",
  "ID - Idaho",
  "IL - Illinois",
  "IN - Indiana",
  "KS - Kansas",
  "KY - Kentucky",
  "LA - Louisiana",
  "MA - Massachusetts",
  "MD - Maryland",
  "ME - Maine",
  "MI - Michigan",
  "MN - Minnesota",
  "MO - Missouri",
  "MS - Mississippi",
  "MT - Montana",
  "NC - North Carolina",
  "ND - North Dakota",
  "NE - Nebraska",
  "NH - New Hampshire",
  "NJ - New Jersey",
  "NM - New Mexico",
  "NV - Nevada",
  "NY - New York",
  "OH - Ohio",
  "OK - Oklahoma",
  "OR - Oregon",
  "PA - Pennsylvania",
  "PR - Puerto Rico",
  "RI - Rhode Island",
  "SC - South Carolina",
  "SD - South Dakota",
  "TN - Tennessee",
  "TX - Texas",
  "UT - Utah",
  "VA - Virginia",
  "VI - Virgin Islands",
  "VT - Vermont",
  "WA - Washington",
  "WI - Wisconsin",
  "WV - West Virginia",
  "WY - Wyoming",
];

const AccountTypes = ["Artist Account", "Fan Account"];
const Months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const getYears = () => {
  let currentYear = new Date().getFullYear();
  let minYear = currentYear - 119;
  let years = [];
  for (let i = currentYear; i >= minYear; i--) {
    years.push(i);
  }
  return years;
};
const Years = getYears();

const Signup = () => {
  const [isArtist, setArtist] = useState(false);
  const [birthMonth, setBirthMonth] = useState(0);
  const [birthYear, setBirthYear] = useState(new Date().getFullYear());
  const [errorMessage, setErrorMessage] = useState('');

  let Days = [];
  const getDays = () => {
    let month = birthMonth + 1;
    let year = birthYear;
    let numOfDays = new Date(year, month, 0).getDate();
    let days = [];
    for (let i = 1; i <= numOfDays; i++) {
      days.push(i);
    }
    Days = days;
  };

  return (
      <div class="signup-page">
        <Row>
          <Col xs={24} sm={24} md={14} lg={14}>
            <div class="signup-container">
              <div class="signup-bg" style={{ backgroundImage: `url(${BackgroundImage})` }}>
                <div class="signup-inner">
                  <div class="signup-form">
                    <div class="logo">
                      <a href="#">
                        <img src={LogoImage} alt="Versenation Logo" />
                      </a>
                    </div>
                    <Alert
                        style={{ display: errorMessage !== '' ? '' : 'none' }}
                        message={errorMessage}
                        type="error"
                        showIcon
                        closable
                    />
                    <Formik
                        initialValues={{
                          firstName: "",
                          lastName: "",
                          birthMonth: "",
                          birthDate: "",
                          birthYear: "",
                          email: "",
                          password: "",
                          passwordAgain: "",
                          city: "",
                          state: "",
                          zip: "",
                          accountType: "",
                          bio: "",
                          genre: "",
                        }}
                        validationSchema={SignupSchema}
                        onSubmit={(signupData) => {
                          console.log(signupData);
                          if(isArtist) {
                            axios.post(URI.authentication.artistSignupURL(), signupData)
                                .then(response => {
                                  console.log(response);
                                })
                                .catch(err => {
                                  console.log(err);
                                  setErrorMessage("Oops! Something went wrong.");
                                });
                          } else {
                            axios.post(URI.authentication.fanSignupURL(), signupData)
                                .then(response => {
                                  console.log(response);
                                })
                                .catch(err => {
                                  console.log(err);
                                  setErrorMessage("Oops! Something went wrong.");
                                });
                          }
                        }}
                    >
                      {({ errors, touched }) => (
                        <div>
                          <Form>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Input class="vn-txt" name="firstName" placeholder={"First Name"} />
                                {errors.firstName && touched.firstName ? (<div class="error">{errors.firstName}</div>) : null}
                              </Col>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Input class="vn-txt" name="lastName" placeholder={"Last Name"}/>
                                {errors.lastName && touched.lastName ? (<div class="error">{errors.lastName}</div>) : null}
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Row gutter={[8, 0]}>
                                  <Col xs={12}>
                                    <Select listHeight={250} class="vn-select" name="birthMonth" placeholder={"Birthday (Month)"} onChange={(option) => setBirthMonth(option)} >
                                      {Months.map((state, index) => (
                                        <Select.Option value={index}>{state}</Select.Option>
                                      ))}
                                    </Select>
                                    {errors.birthMonth && touched.birthMonth ? (<div class="error-date">{errors.birthMonth}</div>) : null}
                                  </Col>
                                  <Col xs={5}>
                                    <Select class="vn-select" name="birthDate" placeholder={"(DD)"} onClick={getDays()}>
                                      {Days.map((day) => (
                                        <Select.Option value={day}>{day}</Select.Option>
                                      ))}
                                    </Select>
                                    {errors.birthDate && touched.birthDate ? (<div class="error-date">{errors.birthDate}</div>) : null}
                                  </Col>
                                  <Col xs={7}>
                                    <Select class="vn-select" name="birthYear" placeholder={"(Year)"} onChange={(option) => setBirthYear(option)} >
                                      {Years.map((year) => (
                                        <Select.Option value={year}>{year}</Select.Option>
                                      ))}
                                    </Select>
                                    {errors.birthYear && touched.birthYear ? (<div class="error-date">{errors.birthYear}</div>) : null}
                                  </Col>
                                </Row>
                              </Col>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Input class="vn-txt" name="email" type="email" placeholder={"E-mail"} />
                                {errors.email && touched.email ? <div class="error">{errors.email}</div> : null}
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Input.Password class="vn-txt" name="password" placeholder={"Password"} />
                                {errors.password && touched.password ? <div class="error">{errors.password}</div> : null}
                              </Col>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Input.Password class="vn-txt" name="passwordAgain" placeholder={"Password Again"} />
                                {errors.passwordAgain && touched.passwordAgain ? <div class="error">{errors.passwordAgain}</div> : null}
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Input class="vn-txt" name="city" placeholder={"City"}/>
                                {errors.city && touched.city ? <div class="error">{errors.city}</div> : null}
                              </Col>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <div class="vn-search-select">
                                  <Select name="state" placeholder={"State"} showSearch={true}>
                                    {States.map((state) => (
                                        <Select.Option key={state} value={state}>
                                          {state}
                                        </Select.Option>
                                    ))}
                                  </Select>
                                </div>
                                {errors.state && touched.state ? <div class="error">{errors.state}</div> : null}
                              </Col>
                            </Row>
                            <Row gutter={[20, 0]}>
                              <Col xs={24} sm={24} md={14} lg={12}>
                                <Input class="vn-txt" name="zip" placeholder={"ZIP"} />
                                {errors.zip && touched.zip ? <div class="error">{errors.zip}</div> : null}
                              </Col>
                              <Col xs={24} sm={24} md={14} lg={12}>  
                                  <Select name="accountType" placeholder={"Account Type"}
                                          onChange={(option) => {option == "Artist Account" ? setArtist(true) : setArtist(false);}} >
                                    {AccountTypes.map((accountType) => (
                                        <Select.Option value={accountType}>{accountType}</Select.Option>
                                    ))}
                                  </Select>  
                                {errors.accountType && touched.accountType ? (<div class="error">{errors.accountType}</div>) : null}
                              </Col>
                            </Row>
                            {isArtist &&
                            <div>
                              <Row>
                                <Col lg={24}>
                                  <div class="vn-txtarea">
                                    <Input.TextArea class="vn-text-area" name="bio" placeholder={"Bio"} />
                                    {errors.bio && touched.bio ? (<div class="error">{errors.bio}</div>) : null}
                                  </div>
                                </Col>
                                <Col lg={24}>
                                  <div class="genere-select">
                                      <label>Genre Range</label>
                                      <Checkbox.Group name="genre" options={GenreRange} />
                                      {errors.genre && touched.genre ? (<div class="error">{errors.genre}</div>) : null}
                                    </div>
                                </Col>
                              </Row>
                            </div>
                            }
                            <Row gutter={[20, 0]}>
                              <Col lg={12}>
                                <button class="form-submt-btn btn" type="submit">Submit</button> 
                              </Col>
                              <Col lg={12}>
                              <button class="form-clear-btn btn" type="submit">Submit</button> 
                              </Col>
                            </Row>                              

                          </Form>
                              <div class="social-login">
                                <p>Continue with</p>
                                <ul>
                                  <li>
                                    <a href="#" class="btn fb-btn">
                                      <span>
                                        <img src={FacebookImage} alt="Facebook Logo" />
                                      </span>{" "}
                                      Facebook
                                    </a>
                                  </li>
                                  <li>
                                    <a href="#" class="btn ggl-btn">
                                      <span>
                                        <img src={GoogleImage} alt="Google Logo" />
                                      </span>
                                      Google
                                    </a>
                                  </li>
                                </ul>
                                <p>
                                  <span class="dots">
                                    <EllipsisOutlined />
                                  </span>
                                </p>
                              </div>
                          </div>
                      )}
                    </Formik>
                    
                  </div>
                </div>
              </div>
            </div>

          </Col>
          
          <Col xs={24} sm={24} md={14} lg={10}></Col>
        </Row>
      </div>
    );
  };
  export default Signup;
