import React from "react";
import { Divider } from "antd";

import ColumnLayout from "../../Layouts/ColumnLayout";
import NavigationBar from "../../Components/NavigationBar";
import GuestNavPanel from "../../Components/GuestNavPanel";
import TrendingArtistList from "../../Components/TrendingArtistList";
import Timeline from "../../Components/TimelineV2";

import "./GuestLandingPage.scss";

const GuestLandingPage = () => {
  return (
    <ColumnLayout
      HeaderContent={<NavigationBar />}
      leftSidebarWidth="280"
      LeftSidebarContent={
        <div className="left-sidebar">
          <div className="nav-panel-container">
            <GuestNavPanel />
          </div>
          <Divider className="divider" />
          <div className="trending-artist-container">
            <TrendingArtistList />
          </div>
        </div>
      }
      BodyContent={
        <div className="body">
          <Timeline />
        </div>
      }
    />
  );
};

export default GuestLandingPage;
