import React, { Component } from "react";
import { List, message } from "antd";
import InfiniteScroll from "react-infinite-scroller";
import TimelinePost from "../TimelinePost";
import TestTimelineData from "./TestTimelineData";

import "./TimelineV2.scss";

class TimelineV2 extends Component {
  state = {
    data: [...TestTimelineData.content],
    loading: false,
    hasMore: false,
  };

  handleInfiniteOnLoad = () => {
    message.info("All posts loaded");
  };

  render() {
    return (
      <div className="main-timeline">
        <InfiniteScroll
          initialLoad={false}
          pageStart={0}
          loadMore={this.handleInfiniteOnLoad}
          hasMore={!this.state.loading && this.state.hasMore}
          useWindow={false}
        >
          <List
            dataSource={this.state.data}
            split={false}
            renderItem={(post) => (
              <List.Item key={post.postId}>
                <TimelinePost
                  imgUrl={post.userInfo.profilePic}
                  username={post.userInfo.userName}
                  profileUrl={null}
                  timestamp={post.updatedAt}
                  caption={post.caption}
                  postType={post.postType}
                  body={post.body}
                />
              </List.Item>
            )}
          ></List>
        </InfiniteScroll>
      </div>
    );
  }
}

export default TimelineV2;
