import React from "react";
import {
  FireReactionButton,
  RaisedFistReactionButton,
  PoopReactionButton,
  SadReactionButton,
  ThumbsDownReactionButton,
} from "../ReactionButton";

import "./ReactionsBar.scss";

const ReactionsBar = () => {
  return (
    <div className="reactions-bar">
      <FireReactionButton />
      <PoopReactionButton />
      <RaisedFistReactionButton />
      <ThumbsDownReactionButton />
      <SadReactionButton />
    </div>
  );
};

export default ReactionsBar;
