import React from "react";
import { Button } from "antd";
import { default as APIClient } from "../../Services/VerseNationAPIClient";

import "./BaseReactionButton.scss";

const BaseReactionButton = ({ glyph, mediaType, mediaId, reactionType }) => {
  const apiClient = new APIClient();
  let reactFn = null;

  switch (mediaType) {
    default:
      reactFn = apiClient.reactToPost.bind(apiClient);
      break;
  }

  return (
    <Button
      className="reaction-btn"
      shape="circle"
      type="text"
      size="medium"
      icon={glyph}
      onClick={async () => {
        await reactFn(mediaId, reactionType);
      }}
    />
  );
};

export default BaseReactionButton;
